.Numi {
  flex-shrink: 0;
  min-width: 244px;
  flex-grow: 1;
  aspect-ratio: .691;
}

.border {
  border: 1px solid var(--color-disabled-hover);
  border-radius: 10px;
  margin-top: 16px;
  padding: 20px;
  width: calc(100%-40px);
}

.image {
  margin-top: -36px;
  aspect-ratio: 1;
}

.picture {
  position: relative;
  border: 1px solid var(--color-disabled-hover);
  border-radius: 50%;
  overflow: hidden;
  width: 100%;
  height: 100%;
  background: var(--bg-color-primary-hover);
}

.picture img {
  max-width: 100%;
  max-height: 100%;
  min-width: 100%;
  min-height: 100%;
  object-fit: cover;
  cursor: pointer;
}

.imgPlaceholder {
  position: absolute;
  width: 100%;
  height: 100%;
  background: white;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
}

.imgPlaceholder div {
  width: 210px;
  font-size: 22px;
  font-weight: 600;
  color: transparent;
  background: #666666;
  background-clip: text;
  text-shadow: 0px 1px 1px rgba(255,255,255,0.5);
}


.timer {
  display: flex;
  gap: 8px;
  justify-content: space-between;
  min-width: 140px;
  min-height: 24px;
  position: relative;
  padding: 5px 8px;
  width: fit-content;
  margin-top: -18px;
  margin-left: 3px;
  border-radius: 3px;
  background: var(--bg-color-primary);
  color: white;
}

.invisible {
  visibility: hidden;
}

.name h4 {
  margin: 12px 0 0;
}

/* empty */
.owner { display: block; }

.spacer {
  border: 1px solid #f2f2f2;
  border-radius: 50%;
  margin: 12px 20px;
}

.price {
  min-width: 60px;
  min-height: 24px;
  display: flex;
  justify-content: flex-start;
  align-items: baseline;
}

.ever {
  color: var(--color-hover);
  font-weight: 600;
  font-size: 16px;
  margin-right: 7px;
}

/* empty */
.dollars { display: block; }

.controls {
  display: flex;
  justify-content: space-between;
}

.controls a {
  padding: 0;
  color: var(--color-active);
}
