.NumiView {
  display: flex;
  padding: 56px;
  gap: 84px;
  justify-content: space-between;
  align-items: flex-start;
}

.picture {
  position: relative;
}

.picture button {
  position: absolute;
  bottom: 32px;
  right: 32px;
  background-color: white;
}

.info {
  flex-grow: 1;
}

.tabs {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 32px;
}

.tabs div {
  user-select: none;
  cursor: pointer;
  color: var(--color-active);
}

.tabs .selected {
  color: var(--color);
  border-bottom: 1px solid var(--color);
}

.creator {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 12px;
}

.creatorAvatar {
  width: 44px;
  height: 44px;
  border-radius: 50%;
  overflow: hidden;
}

.username div:first-child {
  font-size: 14px;
  font-weight: 400;
}

.username div:last-child {
  font-size: 18px;
  font-weight: 600;
}

.details {
  margin-top: 14px;
  margin-bottom: 32px;
}

.history {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 24px
}

.historyEvent {
  display: grid;
  grid-template-columns: 32px 1fr 1fr auto;
  align-items: center;
  justify-items: start;
  column-gap: 20px;
}

.avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background-color: var(--color-hover);
}

.NumiView.zoommed {
  padding: 20px;
  flex-direction: column;
  align-items: stretch;
}

@media (max-width: 1224px) {
  .NumiView {
    padding: 20px;
    flex-direction: column;
    align-items: stretch;
  }
  
  .banner {
    background-position: center 230px;
    background-size: 600px;
    min-height: 590px;
    padding: 20px;
  }

  .banner h1 {
    width: 320px;
  }
  
  .NumizListMenu {
    padding: 20px;
  }
  
  .NumizList {
    padding: 20px;
  }
  
}

.deck{
  perspective: 1000px;
}

.card{
  position: relative;
  transform-style: preserve-3d;
  transition: all .5s linear;
}

.face {
  backface-visibility: hidden;
}

.back {
  position: absolute;
  top: 0;
  width: 100%;
  transform: rotateY(180deg);
}

.front, .back{
  transition: all .5s linear;
}

.flipped {
  transform: rotateY(180deg);
}


