.UploadInput {
  display: block;
  flex: 1;
}

.UploadInput > .invalidMessage {
  visibility: hidden;
  font-size: 18px;
  min-height: 27px;
  color: var(--color-error);
}

.fileInput:invalid ~ .invalidMessage {
  visibility: visible;
}

.fileInput:invalid ~ .uploadField {
  border-color: var(--color-error);
}

.uploadField {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 42px;
  border: 2px dashed var(--color-active);
  /* color: var(--color-active); */
  border-radius: 16px;
  min-width: 376px;
  overflow: hidden;
  font-weight: 600;
  font-size: 22px;
  aspect-ratio: 1.233;
  cursor: pointer;
  color: transparent;
  background: #666666;
  background-clip: text;
  text-shadow: 0px 1px 1px rgba(255,255,255,0.5);
  overflow: hidden;
}

.uploadField.draggedOver {
  border: 2px solid var(--color-hover);
}

.preview {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.uploadField>* {
  display: block;
  text-align: center;
}

.fileInput {
  position: absolute;
  width: 1px;
  height: 1px;
  opacity: 0;
  top: 0;
  left: 0;
  z-index: -1;
}

.label {
  display: block;
  margin-top: 16px;
  margin-bottom: 16px;
}

.uploadControl {
  user-select: none;
}
