.SignInPage {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 56px;
}

.SignInPage .content h1 {
  margin-bottom: 0;
}

.SignInPage p {
  font-size: 18px;
  color: var(--color);
  margin-bottom: 48px;
}

.SignInPage .content button {
  padding-left: 92px;
  padding-right: 92px;
  margin-bottom: 16px;
  min-width: 350px;
}

.QRCode {
  width: max-content;
  padding: 48px;
  border: 2px solid var(--color-hover);
  border-radius: 48px;
}

.image {
  width: max-content;
  padding: 36px;
}

.content {
  max-width: 780px;
}

.fullWidth {
  align-self: stretch;
  margin-bottom: 16px;
}

.golden {
  border-color: var(--color-hover);
  color: var(--color-hover);
}

@media (max-width: 1224px) {
  .SignInPage {
    padding: 20px;
    flex-direction: column;
  }
  .content {
    text-align: center;
  }
  .QRCode {
    margin-bottom: 36px;
  }

}
