.TextInput {
  display: block;
}

.TextInput label {
  display: block;
  font-weight: 600;
  font-size: 22px;
  margin-top: 48px;
  margin-bottom: 16px;
}

.textinput, .textarea {
  font-family: 'Poppins';
  color: var(--color);
  padding: 8px 12px;
  border: 1px solid var(--color-active);
  border-radius: 3px;
  position: relative;
  width: 100%;
  box-sizing: border-box;
}

.measure {
  position: relative;
}

.measure::after {
  display: block;
  position: absolute;
  content: attr(data-measure);
  top: 8px;
  right: 14px;
  color: var(--color-active);
}

.textinput::placeholder, .textarea::placeholder {
  font-family: 'Poppins';
  opacity: .4;
  color: var(--color);
}

.textinput:invalid, .textarea:invalid {
  border-color: var(--color-error);
}

.textarea { 
  resize: none;
}

.invalidMessage {
  color: var(--color-error);
  font-size: 18px;
}

.hintMessage {
  color: var(--color-active);
  font-size: 14px;
}

.invalidMessage, .hintMessage {
  margin-top: 8px;
  min-width: 10px;
  min-height: 32px;
}
