.CreateNumi {
  display: block;
  padding: 56px;
}


.CreateNumi form {
  display: flex;
  flex: 9 1;
  flex-direction: column;
  align-items: stretch;
}

.form {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  align-items: flex-start;
}

.uploadBlock {
  display: flex;
  gap: 20px;
}

.uploadContainer {
  flex-grow: 1;
}

.uploadField {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 42px;
  border: 2px dashed var(--color-active);
  color: var(--color-active);
  border-radius: 16px;
  min-width: 376px;
  overflow: hidden;
  aspect-ratio: 1.233;
}

.uploadField>* {
  display: block;
  text-align: center;
}

.uploadField>input[type="file"] {
  position: absolute;
  width: 1px;
  height: 1px;
  opacity: 0;
  top: 0;
  left: 0;
}

.label {
  display: block;
  margin-top: 16px;
  margin-bottom: 16px;
}

.submit {
  padding-left: 130px;
  padding-right: 130px;
  align-self: flex-start;
}

.preview {
  flex: 5 1;
}

.attention {
  max-width: 504px;
  color: var(--color-active);
  margin-top: 80px;
  margin-bottom: 32px;
}
.attention, .formInvalid {
  margin-top: 80px;
  margin-bottom: 24px;
}

.formInvalid {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 18px;
  padding: 24px;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 400;
  background-color: var(--bg-color-error);
  color: var(--color-error);
}

.formInvalid h3 {
  margin:0;
  font-size: 18px;
  font-weight: 500;
}

.exclamation {
  box-sizing: border-box;
  text-align: center;
  font-size: 24px;
  width: 40px;
  height: 40px;
  padding: 10px;
  padding-top: 2px;
  border: 1px solid var(--color-error);
  border-radius: 5px;
}

@media (max-width: 1224px) {
  .HomePage {
    padding: 20px;
  }

  .uploadBlock {
    flex-direction: column;
  }

  .preview {
    display: none;
  }

  .submit {
    padding-left: 130px;
    padding-right: 130px;
    align-self: unset;
  }
}
